import React, {useState} from "react";
import BarChart from "./components/BarChart";
import { UserData } from "./Data";
import './App.css'

function App() {

  const [userData, setUserData] = useState({
    labels: UserData.map((data) => data.fruta),
    datasets: [
      {
        data: UserData.map((data)=> data.votos)
      }
    ]
  })

  function sumData(index){
    UserData[index].votos = UserData[index].votos + 1
    setUserData({...userData, datasets: [
      {
        data: UserData.map((data)=> data.votos)
      }
    ]})
  }

  function showData() {
    console.log(UserData)
  }

  return (
    <div className="App">
      <div style={{ width: 700}}>
        <BarChart chartData={userData} ></BarChart>
      </div>

      <div>
        <button style={{fontFamily: 'Press Start 2P'}} onClick={() => sumData(1)}>Gaa</button>
        <button onClick={() => showData()}> Show</button>
      </div>
      <div>
        <h1>Hola</h1>
      </div>

      
    </div>
  );
}

export default App;
